let todo =[1,2,3];
function todolist(state=todo,action){
    if(action.type =='ADD_TODO'){
        console.log('添加');
        console.log(action);
        return [...state,action.value];//这里是一个新的
    }
    if(action.type =='DELETE_TODO'){
        console.log('删除');
        console.log(action);
        let arr=[...state];
        arr.splice(action.index,1);
        return arr;
        //或者用state.filter过滤
        // return state.
        // return [...state,action.index];
    }
    if(action.type =='INPUT'){
        console.log(action);
        return [...state,action.value];//这里是一个新的
    }

    return state;//要不就返回原数组
}
export default todolist;
//实现todo功能的一个reducer
//reducer是一个纯函数没有副作用 有相同的输入一定会有相同的输出 date不是每次执行结果不一样 reducer是可以预测的 action的type值一般是大写的
