import React from 'react'
import {connect} from 'react-redux'
// dispatch在connet里面有
const Input = (props) => {
    return (
        <div>
            
            <input type='text' onKeyDown={(e)=>
                {
                    if(e.keyCode==13){
                        props.dispatch({
                            type:'ADD_TODO',
                            value:e.target.value
                        })  
                    }
                }
               
            }>
                {/* 键盘事件 获取输入框的值一般就是e.target,value 然后绑定事件dispatch事件*/}
                
            </input>
        </div>
    )
}

export default connect()(Input)
//在app那里调用
