import React from 'react'
import {connect} from 'react-redux'//这是一个函数
const Todos = (props) => {

    // handleChange = (e)=>{
    //     this.setState({inpValue:e.target.value});
    // }
    // console.log(props);
    return (
        <div>
            {props.data.map((item,i)=><li key={i}>
                {item}
                <button 
                    onClick={
                        ()=>props.dispatch({
                            type:'DELETE_TODO',
                            index:i
                        })
                    }
                >
                        删除
                </button>
            </li>)}
            <button onClick={()=>props.dispatch({
                    type:'ADD_TODO',value:456
                })} >
                添加
            </button>
       
        </div>
       
    )
}
const mapStateToProps = (state)=>{
    return {
        data:state.todo
    }
}
export default connect(mapStateToProps)(Todos) 
//函数的调用是一个函数 函数再传一个一个参数 把组件传进去, connet调用的返回值是一个高阶组件
