import React from 'react';
import { Route ,BrowserRouter as Router,Switch, Redirect} from 'react-router-dom';
import './App.css';
import Mytab from './pages3/Mytab';
import Home from './pages3/Home'
import Gouwuche from './pages3/gouwuche'
import My from './pages3/My'
import Dingdan from './pages3/Dingdan'
import Child from './pages3/Child'
import Search from './pages3/Search';
import list from './pages3/Contents.js'
import Totop from './pages3/Totop';
const App =()=>{
  
  return (
    <Router>
      <Switch>
        <Route exact path='/home' render={()=><Home list={list}/>}></Route>
        <Route path ='/child/:id' render={(props)=><Child {...props} list ={list}/>}></Route>
        <Route path='/home/search' component={Search}></Route>
        <Route path='/gouwuche' component={Gouwuche}></Route>
        <Route path='/my' render={(props)=><My {...props}/>}></Route>
        <Route path='/dingdan' component={Dingdan}></Route>
        <Route component={Home}></Route>
        
        {/* <Route render ={
          ()=>
            <Redirect to='/'/>
        }/> */}

      </Switch>

      {/* <Mytab/> */}
    </Router>
  )
}
  
export default App;