import React from 'react'
import {
    NavLink, Route, Redirect,
    useHistory,
    useLocation,
    useParams,
    useRouteMatch,
    withRouter
} from 'react-router-dom'
import {NavBar,Icon,TabBar,Button} from 'antd-mobile'
import './Home.css'
class Child extends React.Component {
    constructor(props) {
        super(props);
    }

    render(){

        // let lists  = this.props.list;
        let id = this.props.match.params.id;

        return (
            <div className='doc'>
                <Icon type='left' size='lg' onClick={()=>this.props.history.goBack()}/>
               
                <div style={{top:'0px'}}>
                    <img src={this.props.list[id].src} style={{width:'100%'}}/>
                    <p className='title' style={{fontSize:'20px'}}>
                        {this.props.list[id].name}
                    </p>
                    <p className='price' style={{color:'coral',fontSize:'25px'}}>
                    ￥{this.props.list[id].price}
                    <span style={{color:'grey',fontSize:'15px',right:'0px'}}> {this.props.list[id].payed}人已购买</span>
                    </p>
                </div> 

                <div className="bottomBtn">
                  <div className='btn'>
                    <button>
                        <i className='iconfont iconicon_A' ></i>
                        <span>店铺</span>
                    </button>
                    <button>
                        <i className='iconfont iconkefuxiaoxixinxi'></i>
                        <span>客服</span>

                    </button>
                    <button>
                        <i className='iconfont iconshoucang'></i>
                        <span>收藏</span>

                    </button>
                    <button>
                        加入购物车

                    </button>
                    <button>
                        立即购买
                    </button>
                  </div>
                </div>

            </div>
        )
    
      }
    
    }
export default withRouter(Child)


