import {Icon,Button,WingBlank,Carousel,NoticeBar,WhiteSpace,Grid, DatePickerView} from 'antd-mobile'
import {Link,Router,NavLink, Route, Redirect,useHistory,useLocation,useParams,useRouteMatch, withRouter} from 'react-router-dom'
import React from 'react'
import './Home.css'
import Mytab from './Mytab';
import list from './Contents.js'
import Totop from './Totop'
class Home extends React.Component {
    constructor(props){
        super(props);
        this.state={
            data:['1','2','3'],  
            imgHeight: 176,     
            // data1:Array.from(new Array(9)).map((_val, i) => ({
            //     icon: 'https://img.alicdn.com/bao/uploaded/i2/2465351717/O1CN01622T0Y1OYQfoYYqii_!!2465351717.png_240x240.jpg',
            //     text: `商品${i}`,
            //   })) 
        }
    }
    componentDidMount() {
        // simulate img loading
        setTimeout(() => {
          this.setState({
            data: ['https://img.alicdn.com/bao/uploaded/i2/2465351717/O1CN01622T0Y1OYQfoYYqii_!!2465351717.png_240x240.jpg', 'https://img.alicdn.com/bao/uploaded/i3/2465351717/O1CN01ZuJ2Oc1OYQfo75e7k_!!2465351717.png_240x240.jpg', 'https://img.alicdn.com/bao/uploaded/i3/2465351717/O1CN01aNEvc81OYQfwA66cf_!!2465351717.jpg_240x240.jpg'],
          });
        }, 100);
      }
    render() {
        return (
            <div>
                <header className="Search">
                    <div className="header-bd">
                        <a className="placeholder" onClick={()=>{this.props.history.push('/home/search')}}>
                        <span className="text iconfont iconsousuo">寻找宝贝店铺</span>
                        </a>
                    </div>
                </header>
                <Carousel
                    autoplay={true}
                    infinite={true}
                    style={{top:'20px'}}
                >
                    {this.state.data.map(val => (
                        <a
                            key={val}
                            href={val}
                            style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
                        >
                            <img
                                src={val}
                                alt=""
                                style={{ width: '100%', verticalAlign: 'top' }}
                                onLoad={() => {
                                    console.log(val);
                                // fire window resize event to change height
                                window.dispatchEvent(new Event('resize'));
                                this.setState({ imgHeight: 'auto' });
                                }}
                            />
                        </a>
                    ))}
                </Carousel>
            
                <WhiteSpace size="lg" />
                    <NoticeBar mode="link" onClick={() => alert('别这样对我')}>
                        双十一要到了，是时候卸载淘宝了！
                    </NoticeBar>
                <WhiteSpace size="lg" />

                {/* 你可能还喜欢 */}
                <div className='imgs'>
                    <img src="https://img.alicdn.com/tfs/TB1V2eQrKSSBuNjy0FlXXbBpVXa-966-114.png" className="fenimg"/>
                </div>
            
                {/* 商品列表 */}
                <ul className='contents'>
                    {
                        list.map((list,i)=>(
                            <li 
                                key={i} 
                                className='content' 
                                // onClick={()=>props.history.push({
                                //     pathname:'/home/child',
                                //     id:i,
                                //     src:list.src
                                //   })}
                            >
                                <Link to={'/child/'+i}>
                                    <img src={list.src}/>
                                    <p className='title'>
                                        {list.name}
                                    </p>
                                    <p className='price'>
                                        ￥{list.price}
                                        <span style={{color:'grey',fontSize:'11px'}}> {list.payed}人已购买</span>
                                    </p>
                                    
                                </Link>
                            </li>
                        ))
                    }
                </ul>

                {/* 回到顶部 */}
                <Totop></Totop>
                {/* 另一种渲染商品列表的方式 */}
                {/* <Grid   
                        style ={{background:'black'}} 
                        data={this.state.data1} 
                        hasLine={false} 
                        columnNum={2} 
                        itemStyle={{ height: '150px', background: 'pink' ,borderRadius:'20px' ,margin:'10px'}}
                        onClick={(data1)=>{
                            console.log(this.props);
                            return this.props.history.push({
                                pathname:'/child',
                                data:data1,
                                text:this.state.text
                              })
                        }
                            }
                        
                /> */}
                <Mytab/>
            </div>
        )
    }
}

export default withRouter(Home)
