import { Button ,TabBar} from 'antd-mobile';
import {withRouter} from 'react-router-dom'
import React from 'react';


class Mytab extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        selectedTab: '/home',
        hidden: false,
        fullScreen: false,
      };
    }
   
    render() {
      const pathname =this.props.location.pathname;
      return (
        <div style={ {position: 'fixed',bottom:0, width: '100%' }}>
          <TabBar
            unselectedTintColor="#111111"
            tintColor="#33A3F4"
            barTintColor="#f7e5e8"
          >
            <TabBar.Item
              title="首页"
              key="Home"
              icon={
                <i className='iconfont iconHomehomepagemenu'></i>                           
              }
              selectedIcon={
                <i className='iconfont iconHomehomepagemenu'></i>                           
              }
              selected={pathname === '/home' }
              // badge={3}
              onPress={() => {
                console.log(this.props)
                this.props.history.push('/home')
               
              }}
              data-seed="logId"
            >
              
            
           
            </TabBar.Item>
            <TabBar.Item
            icon={
              <i className='iconfont icongouwuche1'></i>                           
              }
            selectedIcon={
              <i className='iconfont icongouwuche1'></i>                           
            }
              title="购物车"
              key="gouwuche"
              selected={pathname === '/gouwuche'}
              onPress={() => {
                this.props.history.push('/gouwuche')
              }}
            >
            </TabBar.Item>
            
           
            <TabBar.Item
            
              icon={
                <i className='iconfont icondingdan'></i>                           
              }
              selectedIcon={
                <i className='iconfont icondingdan'></i>                           
              }
              title="订单列表"
              key="dingdan"
              dot
              selected={pathname === '/dingdan'}
              onPress={() => {
                this.props.history.push('/dingdan')
              }}
            >
            
            </TabBar.Item>
            <TabBar.Item
            icon={
              <i className='iconfont iconwode'></i>                           
            }
            selectedIcon={
              <i className='iconfont iconwode'></i>                           
            }
              title="我的淘宝"
              key="my"
              selected={pathname === '/my'}
              onPress={() => {
                this.props.history.push('/my')
              }}
            >
            </TabBar.Item>
            
            
          </TabBar>
        </div>
      );
    }
  }
  
export default withRouter(Mytab);