import {createStore,combineReducers,applyMiddleware,compose} from 'redux';
//compose就是把多个函数组合在一起变成一个函数
// import todolist from './Todosreducer';
// import homereducer from './Homereducer'
// let rootReducer = combineReducers({todo:todolist,home:homereducer});//把子reducer组合起来 在todos里面就变成了数组
import todo from './Todosreducer'
import home from './Homereducer'
// import thunk from 'redux-thunk' //这是一个中间键
let rootReducer = combineReducers({todo,home});
function logger({ getState }) {
    return (next) => (action) => {
      console.log('will dispatch', action)
  
      // 调用 middleware 链中下一个 middleware 的 dispatch。
      let returnValue = next(action)
  
      console.log('state after dispatch', getState())
  
      // 一般会是 action 本身，除非
      // 后面的 middleware 修改了它。
      return returnValue
    }
    //箭头函数返回了一个箭头函数
  }
//会先走logger thunk 在走到reducer
const thunk = ({ dispatch, getState }) => (next) => (action) => {
    if (typeof action === 'function') {
      return action(dispatch, getState);
    }

    return next(action);
};
const store = createStore(
    rootReducer,
    compose(
        applyMiddleware(logger,thunk),
        window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__()
    )
    );
export default store;
//其实就是状态容器 存着数据 