const homereducer =(state={usename:'15'},action)=>{
    console.log(action);
    switch(action.type){
        case'LOGIN':
            // Object.assign({},state,{username:'123'});
            return {username:action.username};//返回的是一个新的对象 就返回一个用户名
        // case'EXIT':
        //     return state
        default:
            return state;//什么也没有也要返回一个值

    }
}
export default homereducer;