const log=(username)=>{
    return (dispatch)=>{
        let url ='https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/login';
        fetch(url,{
            method:'post',
            headers:{
                'Content-Type':'application/json'
            },
        })
        .then(res=>res.json())
        .then(res=>
            {
                console.log(res);
                // res.data.name?
                // dispatch(
                //     // log(res.data.name)///引入这个函数
                //     {
                //     type:'LOGIN',
                //     username:res.data.name //这里实现换值
                //     }
                // )  
                // :
                dispatch(
                    // log(res.data.name)///引入这个函数
                    {
                    type:'LOGIN',
                    username:res.data.name //这里实现换值
                    }
                )  
        
                // setShow(false);//成功了之后就不显示了
            }
            );
    }
    //
    return {
        type:'LOGIN',
        username:username
    }
}
export {log}
