import React, { Component } from 'react'
import './Totop.css'
export default class Totop extends Component {
    constructor(props){
        super(props);
        this.state = { 
            hasScrolled: false 
        }
        this.onScroll = this.onScroll.bind(this);
    }
    componentDidMount(){
        window.onscroll = this.onScroll;
    }
    onScroll = () => {
        if (document.documentElement.scrollTop > 100 && !this.state.hasScrolled) {
          this.setState({ hasScrolled: true })
        } else if (document.documentElement.scrollTop < 100 && this.state.hasScrolled) {
          this.setState({ hasScrolled: false })
        }
    }
    scrollToTop = () => {
        document.documentElement.scrollTop = 0
    }

    render() {
        return (
    
            <div className='Totop'>
                {this.state.hasScrolled && (
                <div className='top' onClick={this.scrollToTop}>
                    <i className="iconfont icondingbu "></i>
                    <p>顶部</p>
                </div>
                )}
      </div>
        )
    }
}